import isEnvBrowser from './env';

export default async (event, data, mockData, customResource) => {
    const options = {
        method: 'POST',
        headers: {
            'Content-type': 'application/json; charset=UTF-8',
        },
        body: JSON.stringify(data || {})
    }

    if (isEnvBrowser()) {
        return mockData;
    }
    const resourceNameByWindow = window.GetParentResourceName ? window.GetParentResourceName() : 'santa_chat';
    const resourceName = customResource || resourceNameByWindow;
    
    const res = await fetch(`https://${resourceName}/${event}`, options);
    return await res.json();
}

