<script setup>
import EmojiIcon from './components/emojiIcon.vue'
import SendIcon from './components/sendIcon.vue'
</script>
<template>
  <div id="app">
    <div id="chat-area" ref="chatAreaRef">
      <Transition name="slide-up">
        <div class="messages-area" id="scroll" :style="{ 'overflow-y': showInput ? 'auto' : 'hidden' }" v-if="chatOpened"
          ref="messagesRef">
          <div class="message" :key="key" v-for="(msg, key) in filteredMessages">
            <span class="message-type" v-bind:class="{
              chamado: msg.type === 'chamado',
              advertencia: msg.type === 'advertencia',
              Admin: msg.type === 'Admin',
              ajuda: msg.type === 'ajuda',
              megaphone: msg.type === 'megaphone',
            }" :style="msg.custom && msg.custom">
              {{ msg.type }}
            </span>
            <div class="message-content" v-bind:class="{
              chamado: msg.type === 'chamado',
              advertencia: msg.type === 'advertencia',
              Admin: msg.type === 'Admin',
              ajuda: msg.type === 'ajuda',
              megaphone: msg.type === 'megaphone',
            }" :style="msg.custom && msg.custom">
              <span class="message-sendedBy">
                {{ msg.vip }}
                {{ msg.author }}:
              </span>
              <content v-html="msg.content" />
              <button class="delete" v-show="isAdmin" @click="() => deleteMessage(msg.content)">
                <img src="./assets/images/bin.svg" />
              </button>
            </div>
          </div>
        </div>
      </Transition>
      <Transition>
        <div id="input-area" v-if="showInput">
          <div class="input-box">
            <div class="commandPrompt">
              <input type="text" maxlength="250" spellcheck="false" class="input" v-model="message" autofocus
                @keypress.enter.prevent="send" ref="input" placeholder="Digite sua mensagem aqui..." />
            </div>
            <EmojiIcon :color="theme.colors.main[500]" :style="{ opacity: emojiPickerOpened ? '1' : '.5' }"
              @click="emojiPickerOpened = !emojiPickerOpened" class="emoji" />
            <SendIcon :color="theme.colors.main[500]" @click="send" class="send"/>
          </div>
        </div>
      </Transition>
      <div class="filter-types-area" v-if="showInput">
        <button class="chatTypeBackground" @click="() => changeChatType('all')"
          :class="{ selected: selectedChatType === 'all' }">
          Todos
        </button>
        <button class="chatTypeBackground" @click="() => changeChatType('global')"
          :class="{ selected: selectedChatType === 'global' }">
          Global
        </button>
        <button class="chatTypeBackground" @click="() => changeChatType('ajuda')"
          :class="{ selected: selectedChatType === 'ajuda' }">
          Ajuda
        </button>
        <button class="chatTypeBackground" @click="() => changeChatType('local')"
          :class="{ selected: selectedChatType === 'local' }">
          Local
        </button>
        <button class="chatTypeBackground" v-for="(type, key) in chatTypes" :key="key" @click="() => changeChatType(type)"
          :class="{ selected: selectedChatType === type }">
          {{ type }}
        </button>
        <div class="emoji-area" v-show="emojiPickerOpened">
          <div class="emoji-item" @click="() => selectEmoji(emoji)" :key="k" v-for="(emoji, k) in emojiList">
            {{ emoji }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import emojis from "./emojis.json";
import post from "./utils/post";
import attempFetchTheme from "./utils/attemp-fetch-theme";
import isEnvBrowser from "./utils/env";
import DEFAULT_THEME from "./utils/mock-theme";

export default {
  data() {
    return {
      emojiPickerOpened: false,
      emojiList: emojis[1].split(" "),
      blockedMessages: [],
      chatOpened: false,
      showInput: false,
      chatTypes: [],
      isAdmin: isEnvBrowser(),
      selectedChatType: "all",
      listener: null,
      input: null,
      notifyToggled: false,
      message: "",
      messages: [],
      messagesRef: null,
      cachedMessages: [],
      currCachedMsg: 1,
      theme: DEFAULT_THEME,
    };
  },

  watch: {
    messages: {
      handler() {
        if (!this.notifyToggled && !this.showInput) {
          return;
        }

        if (this.showChatTimer) {
          clearTimeout(this.showChatTimer);
        }

        if (this.chatOpened) {
          this.$nextTick(() => {
            const messagesContainer = this.$refs.messagesRef;
            const messagesCount = this.messages.length;
            if (messagesCount > 0 && messagesContainer.scrollHeight) {
              messagesContainer.scrollTop = messagesContainer.scrollHeight;
            }
          });
          return;
        }

        this.$nextTick(() => {
          this.chatOpened = true;
          this.showInput = false;
          const messagesContainer = this.$refs.messagesRef;
          const messagesCount = this.messages.length;
          if ((messagesCount > 0) && messagesContainer.scrollHeight) {
            messagesContainer.scrollTop = messagesContainer.scrollHeight;
          }
        });

        this.showChatTimer = setTimeout(() => {
          this.chatOpened = false;
        }, 300);
        this.resetShowChatTimer();
      },
      deep: true,
    },
  },

  computed: {
    filteredMessages() {
      return this.selectedChatType !== "all"
        ? this.messages.filter((el) => el.type === this.selectedChatType || el.type === "megaphone" || el.type === "Admin")
        : this.messages;
    },
  },

  methods: {
    deleteMessage(content) {
      post("ChatDelete", {
        message: content,
      });
    },

    changeChatType(type) {
      this.selectedChatType = type;
      const messagesObj = this.$refs.messagesRef;
      this.$nextTick(() => {
        messagesObj.scrollTop = messagesObj.scrollHeight;
      });
    },

    isCommand() {
      return this.message[0] === "/";
    },

    getTagAndClear() {
      if (this.message.startsWith("!staff ")) {
        this.message = this.message.replace("!staff ", "");
        return "Admin";
      } else if (this.message.startsWith("!gl ")) {
        this.message = this.message.replace("!gl ", "");
        return "global";
      } else if (this.message.startsWith("!help ")) {
        this.message = this.message.replace("!help ", "");
        return "ajuda";
      } else if (this.message.startsWith("!gr ")) {
        this.message = this.message.replace("!gr ", "");
        return this.chatTypes[0]
      } else if (this.selectedChatType == "all") {
        return "local";
      } else {
        return this.selectedChatType;
      }
    },

    changeNotify() {
      this.notifyToggled = !this.notifyToggled;
    },

    checkBlockedMessages() {
      let hasBlocked = false;
      this.message.split(" ").map((v) => {
        this.blockedMessages.forEach((v) => {
          if (this.message.includes(v)) {
            hasBlocked = true;
            return;
          }
        });
        if (hasBlocked) return;
      });

      return !hasBlocked;
    },

    checkFlood() {
      if (this.isCommand() || this.isAdmin) {
        return true;
      }
      let hasFlood = false;
      this.cachedMessages.map((v) => {
        if (v === this.message) {
          hasFlood = true;
          return;
        }
      });

      return !hasFlood;
    },

    send() {
      if (this.message !== "") {
        if (this.checkBlockedMessages() && this.checkFlood()) {
          if (this.cachedMessages.length > 5) this.cachedMessages.shift();
          this.cachedMessages.push(this.message);

          const tag = this.getTagAndClear()
          post("ChatSubmit", {
            message: this.message,
            tag: tag,
          });

          this.message = "";
          this.hideInput();
        }
      } else {
        this.hideInput();
      }
    },

    keyUp() {
      console.log('keyUp')
      if (
        this.currCachedMsg > 5 ||
        this.currCachedMsg > this.cachedMessages.length
      )
        return;
      this.message = this.cachedMessages[this.cachedMessages.length - this.currCachedMsg];
      this.currCachedMsg++;
    },

    keyDown() {
      if (this.currCachedMsg <= 1) return;
      this.currCachedMsg--;
      this.message = this.cachedMessages[this.cachedMessages.length - this.currCachedMsg];
    },

    clear() {
      this.messages = [];
    },

    selectEmoji(emoji) {
      this.message += emoji;
      this.emojiPickerOpened = false;
    },

    clearShowChatTimer() {
      clearTimeout(this.showChatTimer);
    },

    resetShowChatTimer() {
      this.clearShowChatTimer();
      this.showChatTimer = setTimeout(() => {
        if (!this.showInput) {
          this.chatOpened = false;
        }
      }, 3000);
    },

    hideInput() {
      this.message = "";
      this.showInput = false;
      if (!this.notifyToggled) {
        this.chatOpened = false;
      } else {
        this.resetShowChatTimer();
      }
      post("close");
    },
  },

  destroyed() {
    clearInterval(this.focusTimer);
    window.removeEventListener("message", this.listener);
  },

  mounted() {
    attempFetchTheme((themes) => {
      this.theme = themes;
      document.documentElement.style.setProperty("--color-primary", themes.colors.main[500]);
    });

    this.listener = window.addEventListener("message", (event) => {
      const item = event.data || event.detail;

      switch (item.Action) {
        case "Chat":
          this.chatOpened = true;
          this.showInput = true;
          this.currCachedMsg = 1;
          this.chatTypes = item.Data;
          this.blockedMessages = item.Block;
          this.isAdmin = this.chatTypes.includes("Admin");
          setTimeout(() => {
            this.$nextTick(() => {
              this.$refs.input.focus();
              this.$refs.messagesRef.scrollTop = this.$refs.messagesRef.scrollHeight;
            });
          }, 125);
          break;

        case "Message":
          this.messages.push({
            type: item.Type,
            vip: item.Vip,
            author: item.Author,
            content: item.Message,
            custom: item.Custom,
          });
          break;
        case "Delete":
          this.messages = this.messages.filter(
            (el) => el.content !== item.Message
          );
          break;
        case "Toggle":
          if (!item.toggle) {
            this.clearShowChatTimer();
            this.chatOpened = false;
            this.showInput = false;
          }
          this.notifyToggled = item.toggle;
          break;
        case 'ChatScale':
          const scale = item.scale;
          
          let baseLeftForScale1 = 1.771;
          let baseTopForScale1 = 2.031;


          let baseLeft = baseLeftForScale1 * scale - 2;
          let baseTop = baseTopForScale1 * scale + 1;

          if (scale == 1) {
            baseLeft = baseLeftForScale1;
            baseTop = baseTopForScale1;
          } else {
            baseLeft = baseLeftForScale1 * scale - 2;
            baseTop = baseTopForScale1 * scale + 1;
          }
          
          this.$refs.chatAreaRef.style.left = `${baseLeft}vw`;
          this.$refs.chatAreaRef.style.top = `${baseTop}vw`;

          this.$refs.chatAreaRef.style.transform = `scale(${scale})`;
          break;

        default:
          break;
      }
    });

    window.addEventListener("keydown", (event) => {
      if (event.code == "Escape") {
        this.hideInput();
      } else if (event.code == "ArrowUp") this.keyUp();
      else if (event.code == "ArrowDown") this.keyDown();
      if (isEnvBrowser()) {
        if (event.code == "KeyR") {
          const scale = .8;

          let baseLeftForScale1 = 1.771;
          let baseTopForScale1 = 2.031;


          let baseLeft = baseLeftForScale1 * scale - 2;
          let baseTop = baseTopForScale1 * scale + 1;

          this.$refs.chatAreaRef.style.left = `${baseLeft}vw`;
          this.$refs.chatAreaRef.style.top = `${baseTop}vw`;

          this.$refs.chatAreaRef.style.transform = `scale(${scale})`;
        } else if (event.code == "KeyT") {
          this.chatOpened = true;
          this.showInput = true;
          this.currCachedMsg = 1;
          this.isAdmin = this.chatTypes.includes("Admin");
          setTimeout(() => {
            this.$nextTick(() => {
              this.$refs.input.focus();
              this.$refs.messagesRef.scrollTop = this.$refs.messagesRef.scrollHeight;
            });
          }, 125);
        } else if (event.code == "KeyY") {
          this.showInput = !this.showInput;
          this.chatOpened = false;
        } else if (event.code == "KeyP") {
          this.chatToggle = !this.chatToggle;
        } else if (event.code == "KeyU") {
          this.messages.push({
            type: "global",
            author: "Usuário Teste",
            content: (this.messages.length)+" Lorem <b>ipsum</b> dolor sit amet, consectetur adipiscing elit. Donec euismod, nisl eget ultricies ultrices, nisl nisl tincid",
          });
        }
      }
    });
  }
};
</script>

<style scoped>
#app {
  width: 100vw;
  height: 100vh;
}

#chat-area {
  position: absolute;
  top: 2.031vw;
  left: 1.771vw;
  display: flex;
  flex-direction: column;
  width: 33vw;
}

.messages-area {
  display: flex;
  flex-direction: column;
  gap: 0.26vw;
  max-height: 13.875vw;
  overflow-y: auto;
  direction: rtl;
  padding-left: 0.417vw;
  width: 100%;
  overflow-x: hidden;
}

.message {
  width: 100%;
  display: flex;
  align-items: flex-start;
  height: 100%;
  direction: ltr;
  position: relative;
}

.message-type {
  background: rgba(0, 0, 0, 0.5);
  border-top-left-radius: 0.208vw;
  border-bottom-left-radius: 0.208vw;
  font-weight: 500;
  color: #fff;
  font-size: 0.75vw;
  padding: 0.313vw 0.729vw;
  text-transform: uppercase;
  white-space: nowrap;
  height: 100%;
  letter-spacing: 0.031vw;
  text-shadow: #000000 0px 0px 0.1vw;
}

.message-content {
  width: max-content;
  background: linear-gradient(90deg,
      rgba(0, 0, 0, 0.4) 0%,
      rgba(0, 0, 0, 0.2) 100%);
  border-top-right-radius: 0.208vw;
  border-bottom-right-radius: 0.208vw;
  font-weight: 300;
  font-size: 0.75vw;
  color: rgba(255, 255, 255, 0.9);
  padding: 0.313vw 0.729vw;
  letter-spacing: 0.031vw;
  text-shadow: #000000 0px 0px 0.1vw;
}

.message-content content {
  display: inline;
}

.message-sendedBy {
  color: hsl(0deg 0% 100% / 90%);
}

.message button.delete {
  display: none;
  background: var(--color-primary);
  border: none;
  outline: none;
  cursor: pointer;
  width: 1.25vw;
  height: 1.25vw;
  margin-left: 0.208vw;
  position: absolute;
  right: 0;
  bottom: 0;
  border-top-left-radius: 0.208vw;
  border-bottom-right-radius: 0.208vw;
  opacity: 0.9;
}

.message button.delete img {
  width: 0.677vw;
}

.message:hover button.delete {
  display: flex;
  justify-content: center;
  align-items: center;
}

#input-area {
  display: flex;
  gap: 0.313vw;
  margin-top: 1.094vw;
}

.input-box {
  width: 100%;
  height: 2.083vw;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0.208vw;
  padding: 0 0.729vw;
  display: flex;
  align-items: center;
  gap: 1.302vw;
}

.input {
  flex: 1;
  margin-left: 0.223vw;
  background: none;
  outline: none;
  border: none;
  font-weight: 500;
  font-size: 0.75vw;
  color: #ebebeb;
}

.input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.emoji {
  width: 0.938vw;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.send {
  width: 0.677vw;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.notify-box {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0.208vw;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.083vw;
  cursor: pointer;
}

.notify-box img {
  width: 0.833vw;
}

.filter-types-area {
  display: flex;
  margin-top: 0.677vw;
  gap: 0.417vw;
}

.filter-types-area button {
  padding: 0.313vw 0.417vw;
  color: #fff;
  font-weight: 500;
  font-size: 0.75vw;
  line-height: 0.938vw;
  border-radius: 0.208vw;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  height: max-content;
}

.emoji-area {
  position: absolute;
  flex: 1;
  right: 0;
  width: 15vw;
  height: 9.688vw;
  padding: 0.417vw;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0.208vw;
  display: flex;
  gap: 0.417vw;
  flex-wrap: wrap;
  overflow: auto;
  transition: all 0.2s ease-in-out;
}

.emoji-item {
  cursor: pointer;
  opacity: 0.5;
  transition: 0.2s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.emoji-item:hover {
  opacity: 1;
  transform: scale(1.05);
}

.commandPrompt {
  align-items: center;
  display: flex;
  flex: 1;
  border-radius: 0.208vw;
}

.commandText {
  font-size: 0.75vw;
  color: #fff;
  font-weight: 600;
  padding: 0.208vw;
  border-radius: 0.208vw;
  background-color: rgba(255, 255, 255, 0.08);
  margin-left: 0.573vw;
}

.chatTypeBackground {
  text-transform: uppercase;
  background: rgba(255, 255, 255, 0.3);
  opacity: 0.4;
  letter-spacing: 0.031vw;
  transition: all 0.2s;
}

.selected {
  background: var(--color-primary);
  opacity: 1;
}

.chamado {
  background: rgba(255, 53, 41, 0.7);
}

.Admin {
  background: rgba(255, 53, 41, 0.7) !important;
}

.ajuda {
  background: rgba(246, 157, 39, 0.7);
}

.megaphone {
  background: rgba(127, 0, 247, 0.7);
}

.advertencia {
  background: rgba(255, 53, 41, 0.7);
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}


.slide-up-enter-active,
.slide-up-leave-active {
  transition: all 0.3s ease-in-out;
}

.slide-up-enter-from,
.slide-up-leave-to {
  transform: translateY(-100%);
  opacity: 0;
}
</style>
