import post from './post';
import DEFAULT_THEME from './mock-theme';

export default (callback) => {
  let attemps = 0;
  const fetchTheme = () => {
    try {
      post("getTheme", {}, DEFAULT_THEME, 'vrp').then((theme) => {
        console.log(`themes(chat)[vue]: theme found, using it`);
        callback(theme);
      });
    } catch (e) {
      if (attemps < 5) {
        console.log(`themes(chat)[vue]: attemping ${attemps + 1}/5`)
        setTimeout(fetchTheme, 20000);
      } else {
        console.log(`themes(chat)[vue]: no theme found, using default theme`);
        callback(DEFAULT_THEME);
      }
      attemps++;
    }
  }
  fetchTheme();
}